
.content

	.code-embed-wrapper
		margin-bottom: 15px

	.code-embed-wrapper :not(pre) > code[class*="language-"],
	.code-embed-wrapper pre[class*="language-"], 
	#bbpress-forums .code-embed-wrapper :not(pre) > code[class*="language-"],
	#bbpress-forums .code-embed-wrapper pre[class*="language-"]
		background: #3f403c
		border-bottom: 3px solid #93a739
		border-radius: 3px

	.code-embed-wrapper
		font-size: 12px
		padding: 0

	.code-embed-infos
		background-color: #879e39
		overflow: hidden
		border: 0 none !important
		margin-bottom: -2px
		border-radius: 3px 3px 0 0

		.code-embed-name
			float: left
			color: #fff
			margin: 0
			padding: 5px 10px

			&:hover
				background-color: darken(#879e39, 10%)
				color: #fff

		.code-embed-raw
			float: right
			color: #fff
			margin: 0
			padding: 5px 10px

			&:hover
				background-color: darken(#879e39, 10%)
				color: #fff
