// contact form 7
.wpcf7
    .screen-reader-response
        display: none !important

    .form__text
        &.wpcf7-not-valid
            border-bottom: 3px solid map-get($colors, form_error)

    .wpcf7-form-control-wrap
        .wpcf7-not-valid-tip
            display: none
            padding: 11px 15px
            margin: 10px 0 0
            position: absolute
            bottom: -49px
            z-index: 10
            background-color: map-get($colors, form_error)
            color: #fff
            font-size: map-get($font-size, s)
            border-radius: 3px
            cursor: pointer
            +transition

            &:before
                content: ''
                position: absolute
                top: -6px
                left: 13px
                border-left: 6px solid transparent
                border-right: 6px solid transparent
                border-bottom: 6px solid map-get($colors, form_error)

        &:hover
            .wpcf7-not-valid-tip
                display: block

.wpcf7-response-output
    display: none
    margin: 5px 0
    padding: 25px
    border-radius: 3px
    color: #fff
    text-align: center
    cursor: pointer

    .invalid &,
    .failed &,
    .sent &
        display: block

    .invalid &
        background: map-get($colors, form_error)

    .failed &
        background: map-get($colors, form_error)

    .sent &
        background: map-get($colors, form_success)
