  font-size: 42px;
  margin: 0 0 20px;
}
.section__lead {
  font-size: 20px;
  margin: 0;
}

.tagline:after {
  content: "";
  display: block;
  margin: 20px auto;
  width: 70px;
  height: 0;
  border-bottom: 2px solid #eceff1;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
