}

.footer-content {
  background-color: #E3D0B2;
  background-image: url("./assets/images/bg-footer-desert.jpg");
  background-repeat: repeat;
  background-position: top center;
  padding: 120px 0 160px;
  position: relative;
  z-index: 1;
  padding: 80px 0 160px;
}

.footer-top {
  background-color: transparent;
  background-image: url("./assets/images/bg-theme.png");
  background-repeat: repeat-x;
  background-position: center 0;
  width: 100%;
  height: 346px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.theme-mountains {
  width: 100%;
  height: 346px;
  background-color: transparent;
  background-image: url("./assets/images/bg-theme.png");
  background-repeat: repeat-x;
  background-position: center -346px;
  position: absolute;
  z-index: 9;
}

.theme-sun {
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url("./assets/images/bg-theme.png");
  background-repeat: no-repeat;
  background-position: 0 -706px;
  position: absolute;
  right: 30%;
  top: 10%;
  z-index: 1;
}

[class*=theme-bird] {
  width: 28px;
  height: 16px;
  position: absolute;
  background-color: transparent;
  background-position: 0;
  background-repeat: no-repeat;
  z-index: 2;
  overflow: hidden;
}
[class*=theme-bird]:before {
  content: "";
  width: 28px;
  height: 16px;
  display: block;
  background-color: transparent;
  background-image: url("./assets/images/bg-theme.png");
  background-repeat: no-repeat;
  animation: wings 0.8s steps(7) infinite;
}

.theme-bird1 {
  animation: bird-flight1 34s infinite;
}

.theme-bird2 {
  animation: bird-flight2 27s infinite;
}

.theme-bird3 {
  animation: bird-flight3 54s infinite;
}

.theme-bird4 {
  animation: bird-flight4 46s infinite;
}

.theme-bird5 {
  animation: bird-flight5 21s infinite;
}

.theme-bird6 {
  animation: bird-flight6 55s infinite;
}

.theme-bird7 {
  animation: bird-flight7 34s infinite;
}

.btn--single-cat {
