  opacity: 0 !important;
  box-shadow: none !important;
}

.btn--border-bottom {
  box-shadow: inset 0 -3px 0 0 #72812c !important;
}

.btn--border-top {
  box-shadow: inset 0 3px 0 0 #72812c !important;
}

.btn--border-right {
