  margin-bottom: 15px;
}
.post h2 {
  font-size: 30px;
  margin-top: 30px;
}
.post h2.step::before {
  counter-increment: section;
  content: "Krok " counter(section) ": ";
}
.post h3 {
  font-size: 24px;
}

.post__title {
  font-family: "Open Sans Condensed", sans-serif;
