.wpcf7 .screen-reader-response {
  display: none !important;
}
.wpcf7 .form__text.wpcf7-not-valid {
  border-bottom: 3px solid #a75539;
}
.wpcf7 .wpcf7-form-control-wrap .wpcf7-not-valid-tip {
  display: none;
  padding: 11px 15px;
  margin: 10px 0 0;
  position: absolute;
  bottom: -49px;
  z-index: 10;
  background-color: #a75539;
  color: #fff;
  font-size: 14px;
  border-radius: 3px;
  cursor: pointer;
  transition: all 300ms linear;
}
.wpcf7 .wpcf7-form-control-wrap .wpcf7-not-valid-tip:before {
  content: "";
  position: absolute;
  top: -6px;
  left: 13px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #a75539;
}
.wpcf7 .wpcf7-form-control-wrap:hover .wpcf7-not-valid-tip {
  display: block;
}

.wpcf7-response-output {
  display: none;
  margin: 5px 0;
  padding: 25px;
  border-radius: 3px;
  color: #fff;
  text-align: center;
  cursor: pointer;
}
.invalid .wpcf7-response-output, .failed .wpcf7-response-output, .sent .wpcf7-response-output {
  display: block;
}
.invalid .wpcf7-response-output {
  background: #a75539;
}
.failed .wpcf7-response-output {
  background: #a75539;
}
.sent .wpcf7-response-output {
  background: #95C795;
}

