
.post
	counter-reset: section
	margin: 30px 0 50px

	p
		font-size: 15px

	h2, h3
		font-family: "Open Sans Condensed",sans-serif
		margin-top: 15px
		margin-bottom: 15px

	h2
		font-size: 30px
		margin-top: 30px

		&.step

			&::before
				counter-increment: section
				content: "Krok " counter(section) ": "

	h3
		font-size: 24px

.post__title
	font-family: "Open Sans Condensed",sans-serif
	font-size: 40px
	margin: 0 0 20px
	line-height: 1.3
