$container-max-widths: (
    sm: 540px,
    md: 720px,
    lg: 960px,
    xl: 1340px
) !default;

$grid-breakpoints: (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 992px,
    xl: 1200px,
) !default;

$grid-columns: 12 !default;
$grid-gutter-width: 30px !default;

$colors: (
    first: #272822,
    second: #392929,
    form_error: #a75539,
    form_success: #95C795,
);

$fonts: (
    'family': (
        first: 'Open Sans',
        second: 'Open Sans Condensed',
    ),
    'size': (
        xs: 12px,
        s: 14px,
        m: 17px,
        l: 20px,
        xl: 24px,
        xxl: 36px,
        xxxl: 42px,
    ),
    'color': (
        light: #ffffff,
        dark: #272822,
        first: #77838f,
    ),
);

$button: (
    'default': (
        // 'color': #fff,
        // 'color-hover': #fff,
        // 'background': #93a739,
        // 'background-hover': #d9dfe3,
        'font-family': 'Open Sans',
        'font-weight': 700,
        'text-transform': uppercase,
    ),
    'color': (
        // name, background, color
        ('btn', #93a739, #fff),
        ('btn--second', #272822, #fff),
        ('btn--success', #2fce7f, #ffffff),
        ('btn--error', #ce2f45, #ffffff),
        ('btn--warning', #fde74c, #ffffff),
        ('btn--info', #5bc0eb, #ffffff),
        ('btn--light', #eceff1, #77838f),
        ('btn--dark', #77838f, #eceff1),
        ('btn--link', transparent, #77838f),
    ),
    'size': (
        // name, font-size, padding
        ('btn', 14px, 16px 15px),
        ('btn--s', 11px, 10px),
        ('btn--l', 15px, 20px),
        ('btn--xl', 18px, 25px),
    ),
    'radius': (
        // name, radius
        ('btn--rad-0', 0),
        ('btn--rad-3', 3px),
        ('btn--rad-5', 5px),
        ('btn--rad-10', 10px),
    ),
);

$forms: (
    'form': (
        margin: 0,
    ),
    'form__row': (
        margin: 15px,
    ),
    'form__row--submit': (
        margin: 15px,
        text-align: left,
    ),
    'form__label': (
        margin: 0,
    ),
    'form__text': (
        height: 46px,
        height-textarea: 200px,
        font-family: inherit,
        font-style: normal,
        font-weight: 400,
        text-align: left,
        'colors': (
            // ('name', bg, color, border)
            ('form__text', #3f403c, #d5d7bf, 0 0 3px, solid, #93a739),
        ),
        'sizes': (
            // ('name', font-size, padding )
            ('form__text', 14px, 11px 25px),
            // ('input-text--xs', 5px 15px, 10px),
            // ('input-text--s', 8px 20px, 12px),
            // ('input-text--m', 13px 25px, 13px),
            // ('input-text--l', 15px 30px, 14px),
            // ('input-text--xl', 20px 35px, 16px),
        ),
        'radius': (
            // (name, border-radius)
            ('form__text', 3px),
        ),
    ),
);

$cookies: (
    background: #93a739,
    color: map-get($colors, first),
    color-link: map-get($colors, first),
    button-backgound: #fff,
    button-color: map-get($colors, first),
    font-size: 13px,
);
