

//!
	media min
		+mediamin($screen-xs)
		+mediamin($screen-sm)
		+mediamin($screen-md)
		+mediamin($screen-lg)

=mediamin($breakpoint)

	@media (min-width: $breakpoint)
		@content


//!
	media max
		+mediamax($screen-xs-max)
		+mediamax($screen-sm-max)
		+mediamax($screen-md-max)
		+mediamax($screen-lg-max)

=mediamax($breakpoint)

	@media (max-width: $breakpoint)
		@content


//!
	link
		+link(red, blue)

=link($link, $hover)

	&
		color: $link

		&:hover
			color: $hover


//!
	transition
		+transition()
		+transition(all, 300ms, $function)
		+transition(all, 300ms, linear)
		// [ease, linear, ease-in, ease-out, ease-in-out]
=transition($p: all, $d: 300ms, $t: linear)
	transition: $p $d $t