
$font-family: map-get($fonts, 'family')
$font-color: map-get($fonts, 'color')
$font-size: map-get($fonts, 'size')

html, body
    color: map-get($colors, first)
    background-color: map-get($font-color, white)
    font-family: map-get($font-family, first), sans-serif
    font-size: map-get($font-size, l)
    line-height: 1.42
    font-style: normal
    font-weight: 300

    +media-breakpoint-up(md)
        font-size: map-get($font-size, m)

body
    min-height: 100%
    display: -ms-flexbox
    -ms-flex-direction: column

p
    color: map-get($colors, first)
    font-size: map-get($font-size, l)
    margin-top: 15px
    margin-bottom: 15px
    line-height: 1.42

    +media-breakpoint-up(md)
        font-size: map-get($font-size, m)

a
    color: map-get($colors, first)

.wrapper
    width: 100%
    min-height: 100vh
    display: flex
    flex-direction: column

.page
    flex-grow: 1

.section
    position: relative
    overflow: hidden

    &:first-child
        padding-top: 80px

    &.section--page-top
        min-height: 300px

    &__title
        font-family: map-get($font-family, second), sans-serif
        font-size: map-get($font-size, xxxl)
        margin: 0 0 20px

    &__lead
        font-size: map-get($font-size, l)
        margin: 0

.tagline
    &:after
        content: ""
        display: block
        margin: 20px auto
        width: 70px
        height: 0
        border-bottom: 2px solid #eceff1

.header
    position: fixed
    top: 0
    width: 100%
    z-index: 2
    background-color: #fff
    padding: 10px 0
    box-shadow: 3px 0px 15px 0px rgba(0, 0, 0, 0.05)

.logo
    width: 100%
    margin: 0
    display: block

.logo__image
    width: auto
    opacity: 1
    +transition

    &:hover
        opacity: .8

.offer
    margin-top: 50px

.offer__title
    font-family: "Open Sans Condensed", sans-serif
    font-size: 26px
    margin-top: 15px

.offer__icon
    color: #879e39
    font-size: 60px
    height: 60px
    line-height: 100%

.offer__description
    margin-bottom: 0

// footer

.footer
    position: relative

.footer-content
    background-color: #E3D0B2
    background-image: url("./assets/images/bg-footer-desert.jpg")
    background-repeat: repeat
    background-position: top center
    padding: 120px 0 160px
    position: relative
    z-index: 1

    +media-breakpoint-down(xs)
        padding: 80px 0 160px

.footer-top
    background-color: transparent
    background-image: url("./assets/images/bg-theme.png")
    background-repeat: repeat-x
    background-position: center 0
    width: 100%
    height: 346px
    position: relative
    overflow: hidden
    z-index: 2

.theme-mountains
    width: 100%
    height: 346px
    background-color: transparent
    background-image: url("./assets/images/bg-theme.png")
    background-repeat: repeat-x
    background-position: center -346px
    position: absolute
    z-index: 9

.theme-sun
    width: 40px
    height: 40px
    background-color: transparent
    background-image: url("./assets/images/bg-theme.png")
    background-repeat: no-repeat
    background-position: 0 -706px
    position: absolute
    right: 30%
    top: 10%
    z-index: 1

[class*='theme-bird']
    width: 28px
    height: 16px
    position: absolute
    background-color: transparent
    background-position: 0
    background-repeat: no-repeat
    z-index: 2
    overflow: hidden

    &:before
        content: ''
        width: 28px
        height: 16px
        display: block
        background-color: transparent
        background-image: url("./assets/images/bg-theme.png")
        background-repeat: no-repeat
        +wings

.theme-bird1
    +bird-animate(bird-flight1, 34s)

.theme-bird2
    +bird-animate(bird-flight2, 27s)

.theme-bird3
    +bird-animate(bird-flight3, 54s)

.theme-bird4
    +bird-animate(bird-flight4, 46s)

.theme-bird5
    +bird-animate(bird-flight5, 21s)

.theme-bird6
    +bird-animate(bird-flight6, 55s)

.theme-bird7
    +bird-animate(bird-flight7, 34s)




.btn--single-cat
    margin: 30px 0 15px



// 404

.vulture--404
    display: inline-block
    background-color: transparent
    background-image: url("../images/sprite.png")
    background-repeat: no-repeat
    background-position: -193px -516px
    width: 167px
    height: 109px
    position: relative
    margin-bottom: -56px
    margin-left: -191px

.section__background
    position: absolute
    width: 100%
    height: 100%
    top: 0
    left: 0
    background-image: url('../images/bg.jpg')
    background-repeat: no-repeat
    background-position: center 80px
    background-attachment: fixed
    opacity: .1



// google badge

.grecaptcha-badge
    display: none !important
    visibility: hidden !important
    opacity: 0 !important
    box-shadow: none !important



// borders

.btn--border-bottom
    box-shadow: inset 0 -3px 0 0 #72812c !important

.btn--border-top
    box-shadow: inset 0 3px 0 0 #72812c !important

.btn--border-right
    box-shadow: inset -3px 0 0 #72812c !important

.btn--border-left
    box-shadow: inset 3px 0 0 #72812c !important

.btn--border
    box-shadow: inset 0 0 0 2px #72812c !important
