/* fontello */
@font-face {
    font-family: 'fontello';
    src: url('./assets/fonts/fontello.eot');
    src: url('./assets/fonts/fontello.eot#iefix') format('embedded-opentype'),
             url('./assets/fonts/fontello.woff2') format('woff2'),
             url('./assets/fonts/fontello.woff') format('woff'),
             url('./assets/fonts/fontello.ttf') format('truetype'),
             url('./assets/fonts/fontello.svg#fontello') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    margin-left: .2em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-designing:before { content: '\e800'; }
.icon-mobile:before { content: '\e801'; }
.icon-administration:before { content: '\e802'; }
.icon-html:before { content: '\f13b'; }
.icon-optimization:before { content: '\f201'; }
.icon-cms:before { content: '\f31d'; }
