.cc_container
    position: fixed
    left: 0
    right: 0
    bottom: 0
    background: map-get($cookies, background)
    padding: 15px
    overflow: hidden
    z-index: 1000
    font-size: map-get($cookies, font-size)

    +media-breakpoint-down(sm)
        text-align: center

.cc_logo
    display: none

.cc_message
    margin: 0
    color: map-get($cookies, color)
    font-size: map-get($cookies, font-size)

.cc_more_info
    color: map-get($cookies, color-link)
    font-weight: 700

.cc_btn
    display: inline-block
    float: right
    background: map-get($cookies, button-backgound)
    color: map-get($cookies, button-color)
    padding: 5px 10px
    border-radius: 3px
    margin: -5px 0

    +media-breakpoint-down(sm)
        float: none
        margin: 0 0 10px
